
<template >
<div >
  <v-app   >
  
  <v-app-bar app color="#80bbfd" >
      <h1 class="docteur"> HELP DOCTOR</h1>
   <v-list-item-avatar class="mx-3"   size="60"  link to="/contact"  >
              <v-img src="../../assets/dents.jpg"  ></v-img>
            </v-list-item-avatar>
            
             <v-toolbar-title ></v-toolbar-title>
          <v-spacer></v-spacer>
           <v-btn text  color="white" link to="/">
         Acceuille
          </v-btn>
      
          <v-btn text   v-if=" !user " color="white"  link to="/login">
          se connecter
          </v-btn>
          <v-btn icon   v-if=" user " color="white" link to="/dashbord/chat">
           <v-icon  >
      mdi-message-text
    </v-icon>
    </v-btn>
         
        
       <v-btn text color="white"  link to="/contact">
        contact
      </v-btn>
      
            <v-list-item-avatar   v-if=" user " size="40"  link to="/contact"  >
              <v-img src="https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg"  ></v-img>
            </v-list-item-avatar>
            
          
          
      <v-btn icon color="white" v-if=" user "   @click="logout" >
        <v-icon>mdi-export</v-icon>
      </v-btn>
   
    </v-app-bar>
     
      <v-main>
      <router-view></router-view>
  </v-main>

  </v-app>
  </div>
</template>

<script>


export default {
  name: 'dashbord',
  created: function () {
        let authorization=localStorage.getItem('authorization')
      let user = localStorage.getItem('user');
      user = JSON.parse(user);
      this.user = user
     console.log(user._id)
     console.log(authorization)
      
    },
  data: () => ({
    user: {},
    menuOpen:true,
    disabled:false,
     loading : false,
    drawer:false,
    show:false,
    show1:false,
    show2:false,
    show3:false,
   descriptionLimit: 60,
      entries: [],
      isLoading: false,
      model: null,
      search: null,
   
  }),
  
   methods:{
     
     logout:function(){
      
 
   location.reload();
 localStorage.clear()
   console.log(localStorage)
  this.$router.push('/')
 
        
     
        
  },
   

    computed: {
      islogged() {
      if (this.user) {
        return true
      } else {
        return false
      }
      },
    },
      fields () {
        if (!this.model) return []

        return Object.keys(this.model).map(key => {
          return {
            key,
            value: this.model[key] || 'n/a',
          }
        })
      },
      items () {
        return this.entries.map(entry => {
          const Description = entry.Description.length > this.descriptionLimit
            ? entry.Description.slice(0, this.descriptionLimit) + '...'
            : entry.Description

          return Object.assign({}, entry, { Description })
        })
      },
    },

    watch: {
     
      search () {
        // Items have already been loaded
        if (this.items.length > 0) return

        // Items have already been requested
        if (this.isLoading) return

        this.isLoading = true

        // Lazily load input items
        fetch(' https://production.api-annuaire-sante.fr/professionnel_de_santes')
          .then(res => res.json())
          .then(res => {
            const {  entries } = res
            
            this.entries = entries
          })
          .catch(err => {
            console.log(err)
          })
          .finally(() => (this.isLoading = false))
      },
    },
}
</script>
