<template>
 <v-row justify="center">
 
    <v-dialog v-model="dialog" persistent max-width="500px"  @keydown.esc="cancel">
    
      <v-card >
        <v-card-title>
          <span justify="center" >Ajouter un passion</span>
           
          <v-spacer></v-spacer>
           
       <v-icon  right top @click="dialog = false">mdi-close</v-icon>
        </v-card-title>
         <v-divider class="mt-4"
    
      ></v-divider>
        <v-card-text>
     <v-container >
  <v-form
    ref="form"
    v-model="valid"
    lazy-validation
  >
    <v-text-field
      v-model="name"
     
    color="primary"  label="Name"
     :rules="rule" required
    ></v-text-field>
     <v-text-field
      v-model="lastname"     color="primary"  label="LastName"      :rules="rule" required     ></v-text-field>
<v-text-field
      v-model="adesse"     
    color="primary"  label="Adresse"      :rules="rule"    ></v-text-field>
         <v-select
          v-model="select"
          :items="items"
          :error-messages="errors"
          label="Genre"
          data-vv-name="Select"
         :rules="rule" required
        ></v-select>

   <v-text-field
          v-model="telephone"
           color="primary" 
          :error-messages="errors"
          label="Phone Number"  
         :rules="rule"
        ></v-text-field>
      <v-file-input
     accept="image/*"
      v-on:change="handleFileUpload"
      label="image Patient"
       
    ></v-file-input>
      <v-menu
          v-model="menu1"
          :close-on-content-click="false"
         
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              :value="computedDateFormattedMomentjs"
              clearable
              label="Date Naissance"
              readonly
             width="200px"
              v-bind="attrs"
              v-model="birthday"
              v-on="on"
              @click:clear="date = null"
            prepend-icon="mdi-calendar"
             :rules="rule"
            ></v-text-field>
             
          </template>
          <v-date-picker
            v-model="birthday"
            @change="menu1 = false"
            :rules="rule" required
          ></v-date-picker>
         
        </v-menu>
    

    <v-btn
      color="error"
      class=" mt-16"
      @click="reset" >
      Reset Form
    </v-btn>

  
  </v-form>
  </v-container>
        
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red"  text  @click="dialog = false">
            Close
          </v-btn>
          <v-btn color="blue darken-1"  @click="save_passion" :loading="loading" >
            Save
          </v-btn>
        </v-card-actions>
        <v-snackbar v-model="snackbar" :timeout="timeout">
            <v-alert class="mt-5" text type="success">
              Patient a Ã©tÃ© ajoutÃ© avec succÃ¨s
            </v-alert>
            <template v-slot:action="{ attrs }">
              <v-btn color="blue"  v-bind="attrs" @click="snackbar = false &&this.$refs.form.reset(); dialog = false">
                Fermer
              </v-btn>
            </template>
          </v-snackbar>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import axios from "axios";
  export default {
      props:['ajoutpassion'],
    data: () => ({
         dialog: false,
         menuOpen:false,
      modal: false,
      valid: true,
      menu1: false,
      snackbar: false,
      timeout: 10000,
      loading: false,
      passion:[],
      files: [],
      checkbox: false,
      rule: [
         v => !!v ||"input can be not empty"
         ],
       items: [
         'Male',
         'Female',
         'Autre'
       ]
    }),

    methods:{
        cancel() {
      this.dialog = false
    },
      
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      handleFileUpload(files) {

        this.files[0] = files
      },
       async save_passion(){
          this.loading = true
        let formData = new FormData()
        formData.append('name', this.name)
        formData.append('lastname', this.lastname)
        formData.append('genre', this.select)
        formData.append('adresse', this.adresse)
        formData.append('telephone', this.telephone)
        formData.append('DateNaissance', this.birthday)
        for (let file of this.files) {
            formData.append("imagepassion", file, file.name);
        }
         try {
      let response=   await   axios.post('http://127.0.0.1:3000/user/ajout_passion',formData,{
            headers: {
           
              "Content-Type": "multipart/form-data" }
              })

            if(response.status==200){
              console.log(response)
               this.snackbar = true;
            }

        } catch (error) {
          console.log(error)
        }
        
           this.loading = false
       }
      },
watch: { 
ajoutpassion: function(newVal) { 
          
          this.dialog = newVal
        },
   dialog:function(newval){
          this.$emit('update:ajoutpassion', newval);
        }
      }
  }
</script>