<template>

  <v-app>


    <!-- <v-col  v-for="(tech, i) in  page_tech.fiche.seances" v-bind:key="i" > CCD5AE         -->
    <v-card class="ma-9  mt-16" shaped>
      <v-text-field label="Recherche Patient" append-icon="search" @click:clear="clearSearch" clearable v-model="search"
        single-line outlined dense>
      </v-text-field>
      <v-row align="center" v-if="page_tech.length>0">
        <v-col v-for="(tech,i) in page_tech" :key="i">
          <v-hover v-slot="{ hover }" open-delay="0">
            <v-card  width="220" height="240" :elevation="hover ? 8 : 2" class=" ma-9  mt-16 pa-2 card-manager"
              :class="{ 'on-hover': hover }">
              <v-speed-dial class="bonjour v-speed-dial  " :top="top" :bottom="bottom" :right="right"
                :direction="direction">
                <template v-slot:activator>
                  <v-icon>mdi-dots-vertical</v-icon>



                </template>
                <div class="v-speed-dial__list">
                  <v-btn class="list_button" fab dark small color="#CCD5AE ">
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>

                  <fiche v-bind:openfiche.sync="openfiche"></fiche>
                  <v-btn class="mt-6" fab dark small color="#CCD5AE " @click="confirmdelete(tech._id)">
                    <v-icon>delete_forever</v-icon>
                  </v-btn>

                </div>

              </v-speed-dial>

              <v-avatar class="photo " size="60">
                <img :src="tech.imagepassion">
              </v-avatar>
              <v-divider></v-divider>
              <v-card-title width="100" class="  "> {{tech.name}} {{tech.lastname}}</v-card-title>
              <v-card-subtitle class="  mx-12 layout justify-center ">{{tech.telephone}}</v-card-subtitle>




              <v-btn class="fiche" color="primary" text :to="`/dashbord/one_passion/${tech._id}`">cosulter fiche...
              </v-btn>



            </v-card>
          </v-hover>

        </v-col>
      </v-row>


      <div class="text-center">
        <v-pagination v-model="page" :length="len" :total-visible=len circle></v-pagination>
      </div>
    </v-card>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">

        <v-btn fab v-bind="attrs" v-on="on" color="cyan accent-2" bottom right absolute
          @click.stop="ajoutpassion = true">
          <v-icon>mdi-plus</v-icon>

        </v-btn>

      </template>
      <span>ajout patient</span>

    </v-tooltip>
    <Passion v-bind:ajoutpassion.sync="ajoutpassion"></Passion>
    <confirm ref="confirm"></confirm>
  </v-app>

</template>
<script>
import confirm from "../modals/confirm"
import axios from 'axios';
import moment from 'moment'
import fiche from "../modals/ajout_fiche"
import Passion from "../modals/Passion"
export default {
  name:"ajoutpassion",
  props: ["fiche", "listenerChild"],
 components:{
Passion,
fiche,
confirm
 },
     data () {
      
      return {
        ajoutpassion:false,
        openfiche:false,
           direction: 'bottom',
        dialog: false,
        top:true,
        right:true,
       search: '',
        page:1,
        passion:[],

     
      }
    },
    
       
    mounted(){
    axios.get('http://127.0.0.1:3000/user/affiche_passions',{
       headers: {
            "authorization":localStorage.getItem('authorization')
          }
    })
    .then((response) => {
console.log(response.data);
  
 
 response.data.map(rv=>{
    rv.DateNaissance = moment(new Date(rv.DateNaissance)).format("YYYY-MM-DD");
   
   this.passion.push(rv)
   
  })
    



   
    })
    .catch((error)=>{
      console.log(error);
    })
     
    },
     computed:{
       len() {
            return Math.ceil(this.passion.length / 12)
        },
        page_tech() {
            return this.filterByName(this.passion).slice(this.page * 12- 12, this.page * 12)
        },
     },
    methods:{
       clearSearch(){
            this.search = ''
            console.log(111);
        },
         filterByName: function (techs) {
            if (this.search) {
                return techs.filter(tech => (tech.name+" " +tech.lastname).toLowerCase().includes(this.search.toLowerCase()))
            }
            return techs
        },
       confirmdelete: function (id) {
      this.$refs.confirm.open(`suprimer ${this.passion.Name}`, 'Voulez-vous vraiment supprimer ce patient ?', {
          color: 'red'
        })
        .then(async () => {
  
        
          let response = await axios.post(`http://127.0.0.1:3000/user/delete_passion/${id}`, {}, {
           
          });
          if (response.status == 200) {
           console.log(response)
          }
        })
    },
      
    }
}
</script>
