<script>
import axios from 'axios';
export default {
  data: () => ({
    step: 1,
    show1: false,
    show2: false,
    show3: false,
    loading : false,
     email: '',
                emailrules: [
                    v => !!v || "l'email est requis",
                    v => /.+@.+\..+/.test(v) || "l'email doit Ãªtre valide",
                ],
                password: '',
                passwordrules: [
                    v => !!v || 'Mot de passe est requis',
                    v => (v && v.length >= 6) || 'le mot de passe doit contenir plus de 6 caractÃ¨res',
                ],
                error: false,
               
            
  }),
  props: {
    source: String
  },
  methods:{
     async   logi() {
                this.loading = true    
                let obj = {
                    email : this.email_login,
                    password : this.password_login
                }
              
                try {
                    let response = await axios.post('http://localhost:5000/user/login',obj,{
                   
                    });
                                            
                    if(response.status == 200){
                        
                       localStorage.setItem('authorization', response.data.user.token);
                         localStorage.setItem('authentifier', response.data.user.authentifier);
                    localStorage.setItem('user', JSON.stringify(response.data.user));
                         console.log(response.data.user.token)
                         console.log(response.data.user.role)
                         console.log(localStorage)
                        if(response.data.user.role==="docteur"){
                            
                        this.$router.push('/dashbord/calendrier');
                        }else{
                          this.$router.push('/dashbord/calendrier');
                        }
                    }else{
                        this.error = true
                    }
                } catch (error) {
                    console.log(error)
                        this.error = true
                    
                }
              this.loading = false
            },
            
        },
        options: {
      responsive: true,
        },
        
  }

</script>
