<template>

<v-app  >

  
  <!-- <v-col  v-for="(tech, i) in  page_tech.fiche.seances" v-bind:key="i" > CCD5AE         -->
 <v-card class="ma-9  mt-16"  shaped>
  <v-text-field label="Recherche Patient" append-icon="search" @click:clear="clearSearch" clearable
                                v-model="search" single-line outlined dense>
                            </v-text-field>
 <v-row  align="center" v-if="page_tech.length>0">
<v-col v-for="(tech,i) in page_tech" :key="i"  >
 <v-hover v-slot="{ hover }" open-delay="0">
    <v-card  shaped  width="220" height="240" :elevation="hover ? 8 : 2" class=" ma-9  mt-16 pa-2 card-manager"
    :class="{ 'on-hover': hover }">
 <v-speed-dial
     
 class="bonjour v-speed-dial  "
      
      :top="top"
      :bottom="bottom"
      :right="right"
      
      :direction="direction"
    >
      <template v-slot:activator >
         <v-icon>mdi-dots-vertical</v-icon>
      
          
        
      </template>
      <div class="v-speed-dial__list"> 
      <v-btn
      class="list_button"
        fab
        dark
        small
        color="#CCD5AE "
      >
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
     
        <fiche v-bind:openfiche.sync="openfiche"></fiche>
      <v-btn
      class="mt-6"
        fab
        dark
        small
        color="#CCD5AE "
         @click="confirmdelete"     >
        <v-icon>delete_forever</v-icon>
      </v-btn> 
    
     </div>
      
    </v-speed-dial>
    
     <v-avatar class="photo " size="60" >
                  <img :src="tech.imagepassion" >
                </v-avatar>
                 <v-divider></v-divider>
                  <v-card-title width="100" class="  "> {{tech.name}} {{tech.lastname}}</v-card-title>
                <v-card-subtitle class=" layout justify-center ">{{tech.DateNaissance}}</v-card-subtitle>
                
             
           
                     
 <v-btn class="fiche" color="primary" text  :to="`/dashbord/one_passion/${tech._id}`" >cosulter fiche...</v-btn>
                          
                             
               
              </v-card>
 </v-hover>
 
  </v-col></v-row>
  

<div class="text-center">
    <v-pagination
      v-model="page"
      :length="len"
      :total-visible=len
      circle
    ></v-pagination>
  </div> 
 </v-card>
    <v-tooltip bottom >
      <template v-slot:activator="{ on, attrs }">
 
    <v-btn  fab  v-bind="attrs" v-on="on" color="cyan accent-2" bottom   right absolute @click.stop="ajoutpassion = true" > <v-icon >mdi-plus</v-icon>  
       
    </v-btn>  
    
      </template>
    <span>ajout patient</span>

           </v-tooltip>  
      <Passion v-bind:ajoutpassion.sync="ajoutpassion"></Passion>
    <confirm ref="confirm"></confirm>
</v-app>

</template>
<script>
import confirm from "../modals/confirm"
import axios from 'axios';
import moment from 'moment'
import fiche from "../modals/ajout_fiche"
import Passion from "../modals/Passion"
export default {
  name:"ajoutpassion",
  props: ["fiche", "listenerChild"],
 components:{
Passion,
fiche,
confirm
 },
     data () {
      
      return {
        ajoutpassion:false,
        openfiche:false,
           direction: 'bottom',
        dialog: false,
        top:true,
        right:true,
       search: '',
        page:1,
        passion:[],

     
      }
    },
    
       
    mounted(){
    axios.get('http://localhost:5000/user/affiche_passions',{
       headers: {
            "authorization":localStorage.getItem('authorization')
          }
    })
    .then((response) => {
console.log(response.data);
  
 
 response.data.map(rv=>{
    rv.DateNaissance = moment(new Date(rv.DateNaissance)).format("YYYY-MM-DD");
   
   this.passion.push(rv)
   
  })
    



   
    })
    .catch((error)=>{
      console.log(error);
    })
     
    },
     computed:{
       len() {
            return Math.ceil(this.passion.length / 12)
        },
        page_tech() {
            return this.filterByName(this.passion).slice(this.page * 12- 12, this.page * 12)
        },
     },
    methods:{
       clearSearch(){
            this.search = ''
            console.log(111);
        },
         filterByName: function (techs) {
            if (this.search) {
                return techs.filter(tech => (tech.name+" " +tech.lastname).toLowerCase().includes(this.search.toLowerCase()))
            }
            return techs
        },
       confirmdelete: function () {
      this.$refs.confirm.open(`suprimer ${this.passion.Name}`, 'Voulez-vous vraiment supprimer ce patient ?', {
          color: 'red'
        })
        .then(async () => {
  
        let id=this.passion._id
          let response = await axios.post(`http://localhost:5000/user/delete_passion/${id}`, {}, {
           
          });
          if (response.status == 200) {
           console.log(response)
          }
        })
    },
      
    }
}
</script>
<style scoped>
  #create .v-speed-dial {
    position: absolute;
  }

  #create .v-btn--floating {
    position: relative;
  }
.image{
padding-right: 2%;
}
.marg{
margin-top: 10%;
margin-left: 5%;
}
.marging{
margin-top: 40%;
}
.design{
margin-left: -2px;
}
.bonjour{
padding-left: 90%;
padding-top: 3%;
} 
.pad{
margin-top: 38%;
margin-left: 50%;
}

.photo{
  margin-inline:30%;
  margin-top: -7%;
}
.boton{
margin-inline: 42%;
margin-top:15%;
}
.card{
margin-top: 8%;
}
.fully_card{
box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
}
.spacing_list {
    top: 80%;
}
.v-speed-dial--direction-bottom .v-speed-dial__list{
    align-items: flex-end;
      margin-top: -12%;

}
.list_button{
display: list-item;


}

.fiche{
margin-top: 20%;
}
.card-manager {
    -webkit-transition: all 0.3s ease-in !important;
    -moz-transition: all 0.3s ease-in !important;
    -o-transition: all 0.3s ease-in !important;
    transition: all 0.3s ease-in !important;
     z-index: 1;
    overflow: hidden;
}

.card-manager:hover {
    background-color:	#6495ED !important;
    color: #fff !important;
    box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%) !important;
}
.card-manager::after {
    content: "";
    position: absolute;
    height: 80px;
    width: 80px;
    top: 5%;
    right: -16%;
    transform: translateY(-50%);
   
    transition: 1.8s ease-in-out;
    border-radius: 50%;
    z-index: 0;
}
</style>