<template>
  <v-row justify="center">
   
    <v-dialog v-model="dialog" persistent width="570px"  @keydown.esc="cancel">
    <v-col
     
    >
   
      <v-card ref="form">
       <v-card-title>
         <h1> Ajouter un(e) Assistant(e) </h1>
            <v-divider class="mt-16"></v-divider>
          <v-spacer></v-spacer>
           
       <v-icon  right top @click="dialog = false">mdi-close</v-icon>
        </v-card-title>
        <v-card-text>
         
          <v-text-field
            ref="name"
            v-model="name"
            :rules="[
              () => !!name || 'This field is required',
              () => !!name && name.length <= 25 || 'Address must be less than 25 characters',
             
            ]"
            label="Name"
         
            required
          ></v-text-field>
          <v-text-field
            ref="Lastname"
            v-model="lastname"
            :rules="[() => !!lastname || 'This field is required']"
            label="Lastname"
            required
          ></v-text-field>
          <v-text-field
            ref="email"
            v-model="email"
            :rules="[() => !!email || 'This field is required']"
            label="Email"
            required
         
          ></v-text-field>
          <v-text-field 
            label="Password"
           v-model="password" 
           :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          
            :type="show1 ? 'text' : 'password'"
            @click:append="show1 = !show1"
            
             ></v-text-field>
         
        </v-card-text>
        <v-divider class="mt-12"></v-divider>
        <v-card-actions>
          <v-btn text  @click="dialog = false">
            Cancel
          </v-btn>
          <v-spacer></v-spacer>
          <v-slide-x-reverse-transition>
            <v-tooltip
              v-if="formHasErrors"
              left
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  icon
                  class="my-0"
                  v-bind="attrs"
                  @click="resetForm"
                  v-on="on"
                >
                  <v-icon>mdi-refresh</v-icon>
                </v-btn>
              </template>
              <span>Refresh form</span>
            </v-tooltip>
          </v-slide-x-reverse-transition>
          <v-btn
            color="primary"
            text
            :loading="loading"
            @click="save_assistant"
            
          >
            Submit
          </v-btn>
        </v-card-actions>
      </v-card>
      
    </v-col>
      <v-snackbar v-model="snackbar" shaped :timeout="timeout">
            <v-alert class="mt-5" text color="cyan">
              le compte de l'assistant  a Ã©tÃ© ajoutÃ© avec succÃ¨s
            </v-alert>
            <template v-slot:action="{ attrs }">
              <v-btn color="blue" text v-bind="attrs" @click="snackbar = false &&this.$refs.form.reset(); dialog = false">
                Fermer
              </v-btn>
            </template>
          </v-snackbar>
    </v-dialog>
  </v-row>
</template>
<script>
import axios from 'axios'

  export default {
       props:['ajout_assistant'],
    data: () => ({
      timeout: 10000,
       snackbar:false,
           gialog1:false,
      show1: false,
      errorMessages: '',
      name: '',
      password: '',
      email:'',
      lastname:'',
      city: null,
      state: null,
      loading: false,
       dialog: false,
     menuOpen:false,
      modal: false,
      zip: null,
      country: null,
      formHasErrors: false,
    }),

    computed: {
      form () {
        return {
          name: this.name,
          address: this.address,
          city: this.city,
          state: this.state,
          zip: this.zip,
          country: this.country,
        }
      },
    },
 watch: {
      name () {
        this.errorMessages = ''
      },
ajout_assistant: function(newVal) { 
          
          this.dialog = newVal
        },
   dialog:function(newval){
          this.$emit('update:ajout_assistant', newval);
        }
    },

    methods: {
         cancel() {
      this.dialog = false
    },
    async  save_assistant(){
      this.loading= true
       let obj={
       name:this.name,
       lastname:this.lastname,
       email:this.email,
       password:this.password
       }
        try {
          let response = await axios.post('http://localhost:5000/user/ajout_assistant',obj)


          if(response.status == 200){
            this.snackbar=true
            console.log(response)
          }
        } catch (error) {
          console.log(error)
        }
       this.loading= false   
      },

      addressCheck () {
        this.errorMessages = this.address && !this.name
          ? `Hey! I'm required`
          : ''

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      submit () {
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          this.$refs[f].validate(true)
        })
      },
    },
  }
</script>